@import "../variables";
@import "media-query";

@mixin col() {
  $Col: 12;
  @for $i from 1 through $Col {
    .col-#{$i} {
      width: (100% / $Col * $i); } }

  @for $i from 1 through $Col {
    @include breakpoint(bigDesktop) {
      .col-xl-#{$i} {
        width: (100% / $Col * $i); } } }

  @for $i from 1 through $Col {
    @include breakpoint(smallDesktop) {
      .col-sm-#{$i} {
        width: (100% / $Col * $i); } } } }

@mixin flex() {
  $Flex: 50;
  @for $i from 1 through $Flex {
    .flex-#{$i} {
      flex: $i !important; } } }

@mixin marginSpace() {
  $spaceAmounts: (4, 8, 16, 24, 32, 64);
  $sides: (top, bottom, left, right);
  $singleMargin: 4;

  @each $space in $spaceAmounts {
    @each $side in $sides {
      .m#{str-slice($side, 0, 1)}-#{$space} {
        margin-#{$side}: #{$space}px; }

      .p#{str-slice($side, 0, 1)}-#{$space} {
        padding-#{$side}: #{$space}px; } }

    @each $i in $spaceAmounts {
      .m-#{$space} {
        margin: #{$space}px; }

      .p-#{$space} {
        padding: #{$space}px; }

      .mx-#{$space} {
        margin: 0 #{$space}px; }

      .my-#{$space} {
        margin: #{$space}px 0; }

      .px-#{$space} {
        padding-right: #{$space}px;
        padding-left: #{$space}px; }

      .py-#{$space} {
        padding-top: #{$space}px;
        padding-bottom: #{$space}px; } } } }
